<script>
import Loader from './Loader.svelte';
export let timezone="";
let time='';
let date='';
let H,M,S;
let d;
$:date
$:{
    if(time!='')
    {
     d = new Date();
     d.setHours(time.slice(0,2));
     d.setMinutes(time.slice(3,5));
     d.setSeconds(time.slice(6,8))
     const stop=setInterval(()=>{
        //  if(i==0)
        //  clearInterval(stop);
      d.setSeconds(d.getSeconds()+1);
      H=d.getHours();
      M=d.getMinutes();
      S=d.getSeconds();
     },1000)
    }
    else{
    
    }
    // console.log("changed")
    
}
const get_time= async (timezone)=>{
    time='';
    const response = await fetch(`https://worldtimeapi.org/api/timezone/${timezone}.txt`);
    let data= await response.text();
    return data;
}
get_time(timezone).then(data=>{
    date=data.slice(data.search("datetime")+10,data.search("datetime")+20);
    time=data.slice(data.search("datetime")+21,data.search("datetime")+29);
     
}).catch(err=>console.log("time",err))
</script>


<div class="time">

{#if H!=undefined}
<h4>
{H}:{M}:{S}
</h4>
<h6>
{date}
</h6>
{:else}
<Loader color="brown"/>
 {/if}

</div>

<style>
.time{
    font-family: 'Courier New', Courier, monospace;
}
</style>