<script>
export let accu_key;
export let search_code;
import Loader from './Loader.svelte';
let WeatherText="";
let Temperature="";
let WeatherIcon="";
let day='';

const get_weather= async (search_code)=>{
    const response = await fetch(`https://dataservice.accuweather.com/currentconditions/v1/${search_code}?apikey=${accu_key}`);
    let data= await response.json();
    return data;
}
get_weather(search_code).then(data=>{
    data=data[0];
    WeatherText=data.WeatherText;
    Temperature=data.Temperature.Metric.Value;
    WeatherIcon=`./img/icons/${data.WeatherIcon}.svg`;
    day=data.IsDayTime;
}).catch(err=>{
    console.log("weather",err)
})
</script>


<div class="wrapper">

{#if WeatherIcon!=""}
    <div class="pic">
    {#if day==true}
    <img src="./img/day.svg" alt="day, morning or afternoon time">
    {:else}
    <img src="./img/night.svg" alt="night or evening time sky">
    {/if}
    </div>

    <div class="icon">
    <img src={WeatherIcon} alt="weather icon">
    </div>
    <div class="details">
    <h6 class="center">
    {WeatherText}
    </h6>
    <h5 class="temp">
    <span>{Temperature}</span> &deg;C
    </h5>
    </div>
{:else}
    <Loader color="brown"/>
{/if}

</div>


<style>
.wrapper{
    background-color: white;
    padding:0px;
    border-radius: 10px;
    box-shadow: 0px 2px 4px rgba(0,0,0,0.1);
    border:1px rgb(243, 243, 243) solid;
    width: 300px;
    height:500px;
}
.temp{
    margin-top:30px;
    font-weight: lighter;
    font-size: 35px;
}
.icon{
    text-align: center;
    width:fit-content;
    height: fit-content;
    border-radius: 50%;
    margin: -50px auto 0px auto;
}
.details{
    padding:10px;
}
.icon img{
    height:80px;
    width:80px;
    padding-top:6px;
    padding-bottom: 3px;
    text-align: center;
    background-color: rgb(255, 255, 255);
    border-radius:50%; 
    box-shadow: 0px 2px 3px rgba(0,0,0,0.1);
}
.pic img{
    width:300px;
    height:300px;
    border-radius: 10px;
    
}

@media (max-width: 640px) {
		.pic img{
        width:200px;
        height:200px;
        }
        .temp{
           margin-top:5px;
           font-size: 27px;
        }
        .wrapper{
            margin:5px auto;
            width: 200px;
            height:340px;
        }
	}
</style>